#section-banner{
  background: url('../images/logged-step/banner.jpg') no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 175px;
  padding-bottom: 50px;
  h1{
    display: inline-block;
    font-size: 34px;
    color: #fff;
    font-weight: bold;
    background: #31ca62;
    padding: 3px 30px;
    margin-bottom: 15px;
  }
  h2{
    font-size: 16px;
    color: #4a7699;
    font-weight: bold;
  }
}

.section-find-doctor{
  margin-bottom: 80px;
  .find-doctor-left{
    padding: 30px 0;
    border: 1px solid #CFD8DC;
    border-radius: 5px;
    background: #fff;
    margin-top: 33px;
    @media screen and (max-width: 767px){
      margin-bottom: 30px;
      margin-top: 0;
    }
    h4{
      padding: 0 20px;
      color: #48a7f2;
      font-weight: bold;
      font-size: 14px;
      margin-bottom: 30px;
    }
    hr{
      margin: 0;
    }
    form{
      padding: 0 20px;
      padding-top: 20px;
      .form-group{
        margin-bottom: 15px;
        &.mg-bottom-0{
          margin-bottom: 0;
        }
        label{
          font-size: 13px;
          font-weight: bold;
          color: #3c3c3c;
          margin-bottom: 10px;
        }
        input,select{
          height: 40px !important;
          color: #333333;
        }
        ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: #333333;
        }
        ::-moz-placeholder { /* Firefox 19+ */
          color: #333333;
        }
        :-ms-input-placeholder { /* IE 10+ */
          color: #333333;
        }
        :-moz-placeholder { /* Firefox 18- */
          color: #333333;
        }
        .btn.btn.btn-info{
          margin-top: 10px;
          background: #3c99e3;
          padding: 10px 40px;
          color: #fff;
          border: none;
          text-transform: uppercase;
          border: 1px solid rgb(60, 153, 227);
        }
      }
    }
  }
  .find-doctor-left-1{
    padding: 20px;
    border: 1px solid #CFD8DC;
    border-radius: 5px;
    background: #fff;
    h4{
      color: #3c3c3c;
      font-weight: bold;
      font-size: 14px;
      margin-bottom: 20px;
    }
    hr{
      margin: 0;
      margin-bottom: 20px;
    }
    p{
      color: #3c3c3c;
      font-size: 13px;
      position: relative;
      padding-left: 15px;
      margin-bottom: 20px;
      &.circle{
        &:before{
          content: '';
          border: 1px solid #48a7f2;
          height: 10px;
          width: 10px;
          position: absolute;
          top: 2px;
          left: 0;
          border-radius: 100%;
        }
      }
    }
    ul{
      list-style-type: none;
      margin-bottom: 20px;
      padding-left: 10px;
      li{
        font-size: 12px;
        font-weight: bold;
        color: #48a7f2;
        img{
          margin-right: 5px;
          position: relative;
          top: -2px;
        }
      }
    }
  }
  .find-doctor-right{
    ul.find-doctor-right-top{
      padding-left: 0;
      list-style-type: none;
      marign-bottom: 0;
      li{
        color: #696969;
        font-size: 12px;
        font-weight: bold;
        padding: 0 12px;
        &:nth-child(1){
          width: 5%;
        }
        &:nth-child(2){
          width: 20%;
        }
        &:nth-child(3),&:nth-child(4),&:nth-child(5),&:nth-child(6),&:nth-child(7){
          width: 15%;
        }
      }
    }
    .table-responsive.find-doctor-right-cont{
      position: relative;
      overflow: auto;
      margin: 0;
      margin-bottom: 30px;
      table{
        @media screen and (max-width: 991px){
          min-width: 800px;
        }
        thead{
          background: none;
          border: none;
          th{
            border: none;
          }
        }
        tbody{
          border: 1px solid #CFD8DC;
          background: #fff;
          margin: 0;
          border-radius: 5px;
        }
        tr{
          background: none;
        }
        td{
          border-bottom: 1px solid #CFD8DC;
          border-top: none;
          padding: 15px 12px;
          color: #696969;
          font-size: 14px;
          transition: all .2s ease;
          &:nth-child(7){
            padding: 8px 12px;
            border-right: 2px solid #CFD8DC;
            .btn.btn.btn-info{
              background: #3c99e3;
              padding: 6px 15px;
              width: 100%;
              color: #fff;
              border: none;
              text-transform: uppercase;
              border: 1px solid rgb(60, 153, 227);
            }
          }
        }
      }
    }
  }
  .booking-right-calendar{
    border: 1px solid #CFD8DC;
    background: #fff;
    border-radius: 5px;
    padding: 30px 40px;
    height: auto;
    transition: all .2s ease;
    @media screen and (max-width: 767px){
      padding: 30px 20px;
    }
    .brc-top{
      justify-content: center;
      padding-bottom: 10px;
      margin: -10px -40px 5px -40px;
      border-bottom: 1px solid #CFD8DC;
      position: relative;
      @media screen and (max-width: 767px){
        margin: -10px -40px 5px -40px;
        flex-direction: column;
      }
      .btn-group{
        button{
          outline: none !important;
          box-shadow: none !important;
          border: none !important;
          background: transparent;
          &.btn-default-text{
            font-size: 16px;
            font-weight: bold;
            color: #48a7f2;
          }
          &.btn-default-left{
            margin-right: 20px;
            @media screen and (max-width: 991px){
              margin-right: 10px;
            }
          }
          &.btn-default-right{
            margin-left: 20px;
            @media screen and (max-width: 991px){
              margin-left: 10px;
            }
          }
          i{
            color: #cfd8dc;
            font-size: 30px;
            transition: all .2s ease;
          }
        }
      }
      .brct-note{
        font-size: 12px;
        color: #faa01f;
        font-weight: bold;
        margin-left: 60px;
        position: absolute;
        right: 0;
        @media screen and (max-width: 991px){
          position: relative;
          margin-left: auto;
        }
        @media screen and (max-width: 767px){
          margin-top: 15px;
        }
      }
    }
    .table.calTable{
      margin-bottom: 0;
      tbody{
        tr.brc-scoll{
          td{
            border: none;
            height: 0;
            padding: 0;
          }
          &.show{
            td{
              padding: 20px 10px;
              padding-top: 0;
              height: auto;
            }
            @media screen and (max-width: 991px){
              padding: 10px 5px;
              padding-top: 0;
            }
          }
        }
        td,th{
          width: 14.2857142857%;
          text-align: center;
          color: #3c3c3c;
          font-size: 13px;
          font-weight: bold;
          padding: 20px 10px;
          transition: all .2s ease;
          @media screen and (max-width: 991px){
            font-size: 12px;
            padding: 10px 5px;
          }
        }
        th{
          border-top: 0;
          &.cn{
            color: #fa531f
          }
          &.t7{
            color: #faa01f;
          }
        }
        td{
          transition: all .2s ease;
          &.active{
            background: #eaeff3;
          }
          button{
            background: none;
            box-shadow: none;
            outline: none;
            border: none;
            color: #3c3c3c;
            font-size: 13px;
            font-weight: 700;
            padding: 15px;
            border-radius: 100%;
            width: 45px;
            padding-left: 16px;
            transition: all .2s ease;
            cursor: pointer;
            &.btn-booking-calendar-disable{
              opacity: .4;
              cursor: default;
            }
            &:hover, &:focus, &.active{
              &:not(.btn-booking-calendar-disable){
                color: #fff;
                background: #3ad46b;
              }
            }
          }
        }
      }
    }
    .brc-scroll-group{
      transition: all .3s ease;
      height: 0;
      opacity: 0;
      padding: 0;
      margin: 0;
      border: none;
      visibility: hidden;
      &.show{
        border: 10px solid #eaeff3;
        height: auto;
        opacity: 1;
        margin: 0 -50px;
        padding: 20px 40px;
        visibility: visible;
        @media screen and (max-width: 991px){
          padding: 20px;
        }
        @media screen and (max-width: 767px){
          margin: 0 -20px;
        }
      }
      ul{
        display: flex;
        list-style-type: none;
        padding-left: 0;
        margin: 0;
        align-items: center;
        flex-wrap: wrap;
        @media screen and (max-width: 991px){
          align-items: flex-start;
        }
        &.table-ul-top{
          margin-bottom: 20px;
          @media screen and (max-width: 991px){
            margin-bottom: 0;
          }
          li{
            @media screen and (max-width: 991px){
              margin-top: 0;
            }
          }

        }
        &.table-ul-cont{
          border-top: 1px solid #CFD8DC;
          padding-top: 25px;
          margin-bottom: 25px;
          @media screen and (max-width: 991px){
            margin-bottom: 35px;
          }
          &:hover, &:focus, &.active{
            outline: none;
            li{
              color: #48a7f2;
              font-weight: bold;
            }
            a{
              color: #48a7f2;
              border-color: #48a7f2;
            }
          }
        }
        li{
          width: 40%;
          padding: 0;
          padding-right: 15px;
          font-size: 13px;
          color: #3c3c3c;
          transition: all .2s ease;
          @media screen and (max-width: 991px){
            width: 50%;
          }
          @media screen and (max-width: 767px){
            &:nth-child(2) {
              text-align: right;
            }
          }
          &:last-child{
            @media screen and (max-width: 991px){
              padding-right: 0;
            }
            width: 20%;
            @media screen and (max-width: 991px){
              width: 100%;
              margin-top: 25px;
            }
          }
          a{
            background: none;
            box-shadow: none;
            outline: none;
            border: 1px solid #aeb8c8;
            width: 100%;
            border-radius: 50px !important;
            padding: 8px 15px;
            font-size: 13px;
            font-weight: bold;
            color: #333333;
            margin: -10px 0;
            cursor: pointer;
            transition: all .2s ease;
            cursor: pointer;
            display: block;
            width: 100%;
            text-align: center;
            &:hover, &:focus, &.active{
              color: #48a7f2;
              border-color: #48a7f2;
            }
          }
        }
      }
      .brc-scroll-group-btn{
        background: #eaeff3;
        padding-bottom: 30px;
        margin: 0 -40px;
        margin-bottom: -20px;
        margin-top: 30px;
        @media screen and (max-width: 767px){
          margin: 0 -20px;
        }
        .btn.btn-info{
          font-size: 12px;
          color: #fff;
          font-weight: bold;
          background: #48a7f2;
          padding: 10px 15px;
          border-bottom-left-radius: 15px !important;
          border-bottom-right-radius: 15px !important;
          border: none;
          i{
            margin-left: 10px;
            transform: rotate(270deg);
          }
        }
      }
    }
    .brc-take-off{
      transition: all .2s ease;
      height: 0;
      opacity: 0;
      margin: 0;
      &.show{
        height: auto;
        opacity: 1;
        margin-top: 30px;
      }
      div{
        font-size: 13px;
        color: #4d4d4d;
        padding-left: 20px;
        position: relative;
        &.take-off-red{
          margin-bottom: 10px;
          &:before{
            content: '';
            background: #fa531f;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            position: absolute;
            top: 3px;
            left: 0;
            display: inline-block;
          }
        }
        &.take-off-orange{
          &:before{
            content: '';
            background: #faa01f;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            position: absolute;
            top: 3px;
            left: 0;
            display: inline-block;
          }
        }
      }
    }
    .brc-btn{
      transition: all .2s ease;
      height: 0;
      opacity: 0;
      margin: 0;
      &.show{
        height: auto;
        opacity: 1;
        margin-top: 30px;
      }
      @media screen and (max-width: 991px){
        .btn{
          display: block;
          i{
            top: 10px;
          }
          &.btn-info{
            margin-top: 15px;
            margin-left: 0;
          }
        }
      }
      .btn-success{
        border-radius: 50px !important;
        padding: 12px 30px;
        font-size: 13px;
        color: #fff;
        font-weight: bold;
        border: none;
        background: #38d46a;
        i{
          background: url('../images/booking/delete.png') no-repeat center;
          width: 15px;
          height: 15px;
          display: inline-block;
          margin-left: 10px;
          position: relative;
          top: 3px;
          left: 5px;
        }
      }
      .btn-info{
        background: #48a7f2;
        font-size: 13px;
        color: #fff;
        font-weight: bold;
        padding: 12px 30px;
        margin-left: 20px;
        border: none;
      }
    }
  }
}